@import 'colors';

.discover-list {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  margin-top: 30px;
}

.discover-card {
  width: 250px;
  background-color: $white;
  box-shadow: 0 0 5px 0 #E1E1E1;
  padding: 15px;
  margin-bottom: 15px;
  box-sizing: border-box;
  border-radius: 5px;
  transition: 0.3s;
}

i {
  width: 250px;
  padding: 15px;
  box-sizing: border-box;
}

.filter-form {
  display: flex;
  margin-top: 20px;
  .selects {
    display: flex;
  }
  .custom-select {
    width: 200px;
    .css-1okebmr-indicatorSeparator {
      display: none;
    }
      margin-right: 20px;
  }
}